/* M E N U */

.menu {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  width: 100%;
  height: 50px;
  background: rgba(27,35,42,0.9);
  z-index: 100;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.back {
  position: absolute;
  width: 90px;
  height: 50px;
  top: 0px;
  left: 0px;
  color: rgba(255,255,255,0.5);
  line-height: 45px;
  font-size: 40px;
  padding-left: 10px;
  cursor: pointer;
  transition: .15s all;
}
.back img {
  position: absolute;
  top: 5px;
  left: 30px;
  width: 40px;
  height: 40px;
  background-color: rgba(255,255,255,0.1);
  border-radius: 100%;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  -ms-border-radius: 100%;
  margin-left: 15px;
  }
.back:active {
  background: rgba(0,0,0,0.15);
}
.name {
  position: absolute;
  top: 3px;
  left: 110px;
  font-size: 25px;
  font-weight: 300;
  color: rgba(255,255,255,0.98);
  cursor: default;
}
.last {
  position: absolute;
  top: 30px;
  left: 115px;
  font-size: 11px;
  font-weight: 400;
  color: rgba(255,255,255,0.6);
  cursor: default;
}
.members {
  position: absolute;
  top: 30px;
  left: 115px;
  font-size: 14;
  font-weight: 400;
  color: rgba(255,255,255,0.6);
  cursor: default;
  word-spacing: 2px;
}

/* M E S S A G E S */

.chat {
  list-style: none;
  background: none;
  margin: 0;
  padding: 0 0 50px 0;
  margin-top: 60px;
  margin-bottom: 15px;
}
.chat li {
  padding: 0.5rem;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
}
.chat .day {
  position: relative;
  display: block;
  text-align: center;
  color: rgba(255,255,255,0.3);
  height: 20px;
  text-shadow: 7px 0px 0px #252C33, 6px 0px 0px #252C33, 5px 0px 0px #252C33, 4px 0px 0px #252C33, 3px 0px 0px #252C33, 2px 0px 0px #252C33, 1px 0px 0px #252C33, 1px 0px 0px #252C33, 0px 0px 0px #252C33, -1px 0px 0px #252C33, -2px 0px 0px #252C33, -3px 0px 0px #252C33, -4px 0px 0px #252C33, -5px 0px 0px #252C33, -6px 0px 0px #252C33, -7px 0px 0px #252C33;
  box-shadow: inset 20px 0px 0px #252C33, inset -20px 0px 0px #252C33, inset 0px -2px 0px rgba(255,255,255,0.1);
  line-height: 38px;
  margin-top: 5px;
  margin-bottom: 20px;
  cursor: default;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.chat .notification {
  position: relative;
  display: inherit;
  text-align: center;
  font-size: 13px;
  color: rgba(255,255,255,0.15);
  background: rgba(234, 247, 255, 0.035);
  line-height: 30px;
  border-radius: 100px;
  margin: 7px 35%;
  height: 30px;
  width: 30%;
  box-shadow: 0px 1px 0px rgba(0,0,0,.05), 0px -1px 0px rgba(0,0,0,.05), inset 0px 1px 0px rgba(255,255,255,.02), inset 0px -1px 0px rgba(255,255,255,.02);
  text-shadow: 0px -1px 0px rgba(0,0,0,.1), 0px 1px 0px rgba(255,255,255,.05);
  cursor: default;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  transition: all .2s cubic-bezier(0.565, -0.260, 0.255, 1.410);
}
.chat .notification time {
  position: absolute;
  top: 7px;
  right: 7px;
  font-size: 11px;
  padding: 8px;
  border-radius: 100px;
  background: #252C33;
  box-shadow: 0px 0px 2px rgba(255,255,255,.02), inset 0px 0px 1px rgba(27,35,42,0.1);
  height: 1px;
  line-height: 0px;
  color: rgba(255,255,255,0.1);
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  transition: all .2s cubic-bezier(0.565, -0.260, 0.255, 1.410);
}

.other .msg {
  border-top-left-radius: 0px;
  box-shadow: -1px 2px 0px #c1cbcd;
}
.other:before {
  content: "";
  position: relative;
  top: 0px;
  right: 0px;
  left: 0px;
  width: 0px;
  height: 0px;
  border: 5px solid #eef8ff;
  border-left-color: transparent;
  border-bottom-color: transparent;
}

.self {
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}
.self .msg {
  border-bottom-right-radius: 0px;
  box-shadow: 1px 2px 0px #c1cbcd;
}
.self:after {
  content: "";
  position: relative;
  display: inline-block;
  bottom: 0px;
  right: 0px;
  width: 0px;
  height: 0px;
  border: 5px solid #eef8ff;
  border-right-color: transparent;
  border-top-color: transparent;
  box-shadow: 0px 2px 0px #c1cbcd;
}

.msg {
  background: #eef8ff;
  min-width: 50px;
  padding: 10px;
  border-radius: 2px;
  word-break: break-all;
}
.msg .shield {
  font-size: 20px;
  margin: 0 0 2px 0;
  color: #C23B23;
  font-weight: 700;
  margin-top: -2px;
  margin-bottom: 5px;
  transition: all .2s ease;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.rightChat{
  text-align: right;
}
.leftChat{
  text-align: left;
}
.msg .sword {

  font-size: 20px;
  color:#3F7BB6;
  font-weight: 700;
  margin-top: -2px;
  margin-bottom: 5px;
  transition: all .2s ease;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.msg .user .range.admin {
  display: inline-block;
  font-size: 10px;
  font-weight: 300;
  color: #6aea96;
  padding: 2px;
  border-radius: 3px;
  border: solid 1px #6aea96;
  background: rgba(255,255,255,0);
  margin-left: 5px;
}
.msg p {
  font-size: 16px;
  margin: 0 0 2px 0;
  transition: all .2s ease;
}
.msg img {
  position: relative;
  display: block;
  width: 600px;
  border-radius: 5px;
  box-shadow: 0px 0px 3px #eee;
  transition: all .8s cubic-bezier(0.565, -0.260, 0.255, 1.410);
  cursor: default;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.msg time {
  color: #777;
  font-size: 1rem;
  margin-top: 3px;
  cursor: default;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

::-webkit-scrollbar {
  min-width: 12px;
  width: 12px;
  max-width: 12px;
  min-height: 12px;
  height: 12px;
  max-height: 12px;
  background: #252C33;
  box-shadow: inset 0px 50px 0px rgba(27,35,42,0.9), inset 0px -50px 0px #eee;
}

::-webkit-scrollbar-thumb {
  background: rgba(255,255,255,0.2);
  border: none;
  border-radius: 100px;
  border: solid 3px #252C33;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(255,255,255,0.1);
}

::-webkit-scrollbar-thumb:active {
  background: rgba(255,255,255,0.1);
}

::-webkit-scrollbar-button {
  display: block;
  height: 26px;
}

/* T Y P E */
.typezone{
  position: fixed;
  bottom: 0px;
  width: 75%;
  height: 50px;
  z-index: 99;
  border: none;
  outline: none;
}
textarea, textarea:hover {
  position: absolute;
  bottom: 0px;
  left: 0%;
  right: 8%;
  width: 80%;
  height: 30px;
  z-index: 100;
  background: #fafafa;
  border: none;
  outline: none;
  padding-left: 2%;
  padding-right: 2%;
  padding-top: 2%;
  color: #666;
  font-size: 20px;
  font-weight: 400;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  overflow: hidden;
  resize: none;
  z-index: 200;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
  transition: all .4s cubic-bezier(0.565, -0.260, 0.255, 1.410);
}
textarea:focus {
  height: 200px;
  box-shadow: 0px 0px 20px rgba(0,0,0,0.3);
}
.send {
  position: fixed;
  display: block;
  bottom: 0px;
  right: 10%;
  width: 15%;
  height: 50px;
  border: none;
  outline: none;
  z-index: 100;
  cursor: pointer;
  font-size: 20px;
  background-repeat: no-repeat;
  background-size: 34px 34px;
  background-position: 45% 9px;
  background: rgba(27,35,42,0.9);
  color: white;
  border-radius: 5px 5px 5px 5px;
  margin: 5px 5px 0px 5px;
  transition: all 0.3s ease-in-out;
}
.backToLogin {
  position: fixed;
  display: block;
  top: 0px;
  right: 10%;
  width: 15%;
  height: 50px;
  border: none;
  outline: none;
  z-index: 100;
  cursor: pointer;
  font-size: 20px;
  background-repeat: no-repeat;
  background-size: 34px 34px;
  background-position: 45% 9px;
  background: rgba(27,35,42,0.9);
  color: white;
  border-radius: 5px 5px 5px 5px;
  margin: 0px 5px 0px 5px;
  transition: all 0.3s ease-in-out;
}
.send:hover {
  background-color:rgba(27,35,42,1);
}
.backToLogin:hover {
  background-color:rgba(27,35,42,1);
}
/* R E S P O N S I V E   C O N F I G U R A T I O N */

@media screen and (max-width: 750px) {
  ::-webkit-scrollbar {
  display: none;
  }
  .chat{
      margin-bottom: 55px;
  }
  .msg p {
      font-size: 18px;
  }
  .msg .user {
      font-size: 20px;
  }
  .msg img {
      width: 300px;
  }
  .chat .notification {
      font-size: 12px;
      margin: 7px 30%;
      width: 40%;
  }
  .chat .day {
      font-size: 11px;
  }
  .emojis {
      width: 25%;
  }
  .send {
      width: 25%;
  }
  textarea {
      left: 0px;
      right: 0px;
      bottom: 50px;
      padding-left: 5%;
      padding-right: 5%;
      padding-top: 20px;
      width: 90%;
      border-radius: 0px;
      height: 28px;
      background: #fafafa;
      box-shadow: none;
      transition: all .4s cubic-bezier(0.2, -0.2, 0.2, 1.2);
  }
  textarea:focus {
      height: 30vh;
      margin-top: 30vh;
      box-shadow: 0px -20px 20px rgba(0,0,0,0.1);
  }
  form:focus ~ .typezone {
      bottom: 50vh;
  }
}
@media screen and (max-width: 550px) {
  .msg p {
      max-width: 250px;
  }
  .msg img {
      width: 200px;
  }
  .chat .notification {
      font-size: 12px;
      margin: 7px 0px;
      width: 100%;
      border-radius: 0px;
  }
  .chat .notification time {
      right: 10px;
  }
}
